
	@import 'styles/utils/vars.scss';
	.pay-submit-button{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 600;
		font-size: 28px;
		border-radius: 14px;
		padding: 10px;
		cursor: pointer;
		border: 1px solid #232020;
		color: #232020;
	}
	.pay-submit-form{
		display: none;
	}
	.tos{
		&__checkbox-wrapper {
			display: flex;
			align-items: center;
			margin-bottom: 25px;
		}
		&__checkbox-tick {
			cursor: pointer;
			width: 20px;
			height: 20px;
			border: 1px solid #FFFFFF;
			border-radius: 4px;
			margin-right: 15px;
			display: flex;
			justify-content: center;
			align-items: center;
			img{
				width: 80%;
				height: auto;
			}
		}
		&__checkbox-text {
			font-family: 'Chivo';
			font-style: normal;
			font-weight: 400;
			font-size: 12px;
		}
	}
	.shopping-cart-window{
		top: 25px;
		position: sticky;
		// height: calc(100vh - 50px);
		@media screen and (max-width: 1200px) {
			height: auto;
		}
		overflow-y: auto;
		padding: 15px;
		scrollbar-width: thin;
		&::-webkit-scrollbar {
			width: 15px;
			@media screen and (max-width: $phoneWidth){
				width: 10px;
			}
		}

		&::-webkit-scrollbar-track {
			background-color: $background-color;
			border-radius: 5px;
		}

		&::-webkit-scrollbar-thumb {
			background-color: #555;
			border: 3px solid transparent;
			border-radius: 5px;
			background-clip: content-box;
		}
	}
	.checkout {
		display: flex;
		@media screen and (max-width: 1200px) {
			flex-direction: column;
		}
		&__window{
			width: 50%;
			&--mt{
				margin-top: 28px;
				@media screen and (max-width: 1200px) {
					margin-top: 0;
				}
			}
			@media screen and (max-width: 1200px) {
				width: 100%;
			}
			
		}
		&__fields{
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			margin-top: 25px;
		}
		&__field-title	{
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 14px;
			line-height: 16px;
			margin-bottom: 15px;
		}
		&__field{
			flex: 0 0 calc(50% - 15px);
			@media screen and (max-width: $phoneWidth) {
				flex: 0 0 100%;
			}
		}
	}
	.payment-selection {
		&__loader {
			width: 100%;
			height: 40vh;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		&__methods {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
		}
		&__method {
			flex: 0 0 25%;
			@media screen and (max-width: $smTabletWidth) {
				flex: 0 0 33%;
			}
			padding: 15px;
			cursor: pointer;
			filter: drop-shadow(0 0 0 transparent);
			transition: all 0.5s;
			&.active{
				filter: drop-shadow(0 0 10px var(--shadowColor))
			}
			img{
				width: 100%;
				height: auto;
			}
		}
	}
	
	.lds-roller {
	  display: inline-block;
	  position: relative;
	  width: 80px;
	  height: 80px;
	}
	.lds-roller div {
	  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	  transform-origin: 40px 40px;
	}
	.lds-roller div:after {
	  content: " ";
	  display: block;
	  position: absolute;
	  width: 7px;
	  height: 7px;
	  border-radius: 50%;
	  background: #fff;
	  margin: -4px 0 0 -4px;
	}
	.lds-roller div:nth-child(1) {
	  animation-delay: -0.036s;
	}
	.lds-roller div:nth-child(1):after {
	  top: 63px;
	  left: 63px;
	}
	.lds-roller div:nth-child(2) {
	  animation-delay: -0.072s;
	}
	.lds-roller div:nth-child(2):after {
	  top: 68px;
	  left: 56px;
	}
	.lds-roller div:nth-child(3) {
	  animation-delay: -0.108s;
	}
	.lds-roller div:nth-child(3):after {
	  top: 71px;
	  left: 48px;
	}
	.lds-roller div:nth-child(4) {
	  animation-delay: -0.144s;
	}
	.lds-roller div:nth-child(4):after {
	  top: 72px;
	  left: 40px;
	}
	.lds-roller div:nth-child(5) {
	  animation-delay: -0.18s;
	}
	.lds-roller div:nth-child(5):after {
	  top: 71px;
	  left: 32px;
	}
	.lds-roller div:nth-child(6) {
	  animation-delay: -0.216s;
	}
	.lds-roller div:nth-child(6):after {
	  top: 68px;
	  left: 24px;
	}
	.lds-roller div:nth-child(7) {
	  animation-delay: -0.252s;
	}
	.lds-roller div:nth-child(7):after {
	  top: 63px;
	  left: 17px;
	}
	.lds-roller div:nth-child(8) {
	  animation-delay: -0.288s;
	}
	.lds-roller div:nth-child(8):after {
	  top: 56px;
	  left: 12px;
	}
	@keyframes lds-roller {
	  0% {
	    transform: rotate(0deg);
	  }
	  100% {
	    transform: rotate(360deg);
	  }
	}


