
	@import 'styles/utils/vars.scss';
	.wide-container{
		max-width: 1440px;
		margin: auto;
		padding: 0 50px;
		@media screen and (max-width: $tabletWidth) {
			padding: 0 15px;
		}
	}
	.microphone {
		&--desktop{
			@media screen and (max-width: $smTabletWidth) {
				display: none;
			}
		}
		&--mobile{
			@media screen and (min-width: $smTabletWidth) {
				display: none;
			}
		}
		position: relative;
		z-index: -1;
		width: 100%;
		aspect-ratio: 1/1;
		display: flex;
		justify-content: center;
		align-items: center;
		@media screen and (max-width: $smTabletWidth) {
			margin: auto;
			width: 70%;
		}
		&__outer-wrapper {
			width: 80%;
			height: 80%;
			display: flex;
			justify-content: center;
			align-items: center;
			position: relative;
			overflow: hidden;
			border-radius: 50%;
			// border: 15px solid var(--highlight-color, #FFF);
			// background: var(--highlight-color, #FFF);
			// box-shadow: 4px 4px 15px var(--highlight-color, #FFF);
		}
		&__circle-section{
			z-index: 0;
			position: absolute;
			width: 100%;
			height: 100%;
			transition: all 0.4s;
			background: var(--color);
			transform: rotate(var(--offset)) translateX(70%) rotate3d(1, 0, 0, var(--transformAxisRotation)) rotate(45deg);

		}
		&__inner-wrapper {
			z-index: 1;
			width: calc(100% - 35px);
			height: calc(100% - 35px);
			border-radius: 50%;
			background: $background-color;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		&__image {
			// transition: all 0.5s;
			// transform: rotate(var(--desired-rotation));
			width: 60%;
			height: 60%;
			img{
				height: 100%;
				width: 100%;
				object-fit:contain;
				transform: translateX(9%);
			}
		}
	}

	.booking {
		display: flex;
		justify-content: space-around;
		@media screen and (max-width: $tabletWidth) {
			flex-wrap: wrap;
		}
		
		&__rooms {
			height: 500px;
			flex: 0 0 190px;
			display: flex;
			flex-direction: column;
			margin-right: 50px;
			@media screen and (max-width: $smDesktopWidth) {
				margin-right: 15px;
			}
			@media screen and (max-width: $tabletWidth) {
				order: 1;

				flex: 0 0 100%;
				margin: 0;
				margin-top: 25px;
				margin-bottom: 75px;
			}

		}
		&__room {
			@media screen and (max-width: $tabletWidth) {
				overflow: hidden;
				border-radius: 5px;
			}
			display: flex;
			flex-direction: column;
			flex: 5 1 0;
			transition: all 0.4s;
			filter: grayscale(1);
			cursor: pointer;
			.booking__room-title{
				background: #555;
			}
			&:hover{
				flex: 6 1 0;
				filter: grayscale(0);
				.booking__room-title{
					background: var(--roomColor);
				}
			}
			&.open{
				filter: grayscale(0);
				flex: 10 1 0;
				.booking__room-title{
					background: var(--roomColor);
					color: var(--textColorBG);
				}
			}
			&.closed{
				//no styles for now xd
			}
		}
		&__room-image {
			flex-grow: 1;
			height: 0;
			&>img{
				height: 100%;
				width: 100%;
				object-fit:cover;
			}
		}
		&__room-title {
			flex: 0 0 50px;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 0 0 5px 5px;
			transition: all 0.5s;
		}

		&__calendar-column {
			z-index: 5;
			flex: 0 0 255px;
			display: flex;
			flex-direction: column;
			margin-right: 50px;
			@media screen and (max-width: $smDesktopWidth) {
				margin-right: 15px;
			}
			@media screen and (max-width: $tabletWidth) {
				margin: 0 auto;
			}
			@media screen and (max-width: $smTabletWidth) {
				flex: 0 0 100%;
			}
		}
		&__time-selector {
			flex: 1 0 0;
			max-width: 540px;
			
			@media screen and (max-width: $tabletWidth) {
				margin: auto;
				padding: 0 15px;
				max-width: 410px;
			}
		}
		&__people-selector-row{
			margin-top: 25px;
			padding: 0 25px;
			display: flex;
			justify-content: space-between;
			@media screen and (max-width: $phoneWidth) {
				justify-content: center;
				flex-direction: column;
				align-items: center;
				padding: 0;
			}
		}
		&__price-wrapper {
			align-self: start;
			
			font-family: 'Chivo';
			font-style: normal;
			font-weight: 900;
			font-size: 14px;
			line-height: 17px;
			flex: 0 1 160px;
			@media screen and (max-width: $phoneWidth) {
				margin-top: 15px;
				flex: auto;
				width: 160px;
			}
		}
		&__price {
			width: 100%;
			margin-top: 10px;
			padding: 4px 0;
			border: 1px solid #FFFFFF;
			filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
			border-radius: 8px;
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 14px;
			line-height: 16px;
			display: flex;
			justify-content: center;
		}
		&__next-button-wrapper {
			display: flex;
			justify-content: flex-end;
			margin-top: 15px;
		}
		&__next-button {
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 32px;
			line-height: 38px;
			color: #232020;
			padding: 15px 50px;
			background: #FFFFFF;
			border-radius: 8px;
			cursor: pointer;
			transition: all 0.3s;
			@media screen and (max-width: $smTabletWidth) {
				text-align: center;
				width: 100%;
			}
			&.disabled{
				cursor: default;
				background: #777;
			}
			&:not(.disabled){
				&:hover{
					box-shadow: 0 0 20px 1px rgba(255,255,255,0.5);
				}
			}
		}

	}

	.loader{
		display: flex;
		min-height: 50vh;
		justify-content: center;
		align-items: center;
		.lds-roller {
		  display: inline-block;
		  position: relative;
		  width: 80px;
		  height: 80px;
		}
		.lds-roller div {
		  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
		  transform-origin: 40px 40px;
		}
		.lds-roller div:after {
		  content: " ";
		  display: block;
		  position: absolute;
		  width: 7px;
		  height: 7px;
		  border-radius: 50%;
		  background: #fff;
		  margin: -4px 0 0 -4px;
		}
		.lds-roller div:nth-child(1) {
		  animation-delay: -0.036s;
		}
		.lds-roller div:nth-child(1):after {
		  top: 63px;
		  left: 63px;
		}
		.lds-roller div:nth-child(2) {
		  animation-delay: -0.072s;
		}
		.lds-roller div:nth-child(2):after {
		  top: 68px;
		  left: 56px;
		}
		.lds-roller div:nth-child(3) {
		  animation-delay: -0.108s;
		}
		.lds-roller div:nth-child(3):after {
		  top: 71px;
		  left: 48px;
		}
		.lds-roller div:nth-child(4) {
		  animation-delay: -0.144s;
		}
		.lds-roller div:nth-child(4):after {
		  top: 72px;
		  left: 40px;
		}
		.lds-roller div:nth-child(5) {
		  animation-delay: -0.18s;
		}
		.lds-roller div:nth-child(5):after {
		  top: 71px;
		  left: 32px;
		}
		.lds-roller div:nth-child(6) {
		  animation-delay: -0.216s;
		}
		.lds-roller div:nth-child(6):after {
		  top: 68px;
		  left: 24px;
		}
		.lds-roller div:nth-child(7) {
		  animation-delay: -0.252s;
		}
		.lds-roller div:nth-child(7):after {
		  top: 63px;
		  left: 17px;
		}
		.lds-roller div:nth-child(8) {
		  animation-delay: -0.288s;
		}
		.lds-roller div:nth-child(8):after {
		  top: 56px;
		  left: 12px;
		}
		@keyframes lds-roller {
		  0% {
			transform: rotate(0deg);
		  }
		  100% {
			transform: rotate(360deg);
		  }
		}
	}

