
.item {
	cursor: pointer;
	width: 180px;
	margin: 10px;
	display: flex;
	flex-direction: column;
	transition: all 0.4s;
	&.active{
		transform: scale(1.05);
		.item__content{
			background: var(--highlightColor);

		}
		.item__title{
			color: var(--textColorBG);
		}
		.item__price{
			color: var(--textColorBG);
		}
	}
	&__image {
		height: 180px;
		img{
			height: 100%;
			width: 100%;
			object-fit:cover;
		}
	}
	&__content {
		position: relative;
		transition: all 0.5s;
		flex-grow: 1;
		background: #3A3838;
		border-radius: 0px 0px 6px 6px;
		padding: 10px;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;

	}
	&__title {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 600;
		font-size: 16px;
		line-height: 20px;
		margin-bottom: 10px;
	}
	&__price {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 12px;
		line-height: 20px;
		margin-bottom: 10px;
	}
	&__count-selector {
		position: relative;
		select{
			background: #FFFFFF;
			border-radius: 10px;		
			-webkit-appearance: none;
			-moz-appearance: none;
			border: none;
			color: #232020;
			padding: 7px 15px;
			padding-right: 25px;
			// margin-right: 15px;
		}
	}
	&__count-order-wrapper{
		display: flex;
		justify-content: space-around;
		align-items: center;
		margin-bottom: 10px;

	}
	&__count-selector-arrow {
		position: absolute;
		top: 7px;
		bottom: 7px;
		right: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		pointer-events: none;
		img{

			width: 12px;
		}
	}
	&__description {
		width: 100%;
		input{
			
			border-radius: 10px;
			text-align: center;
			width: 100%;
			padding: 6px 0;
			border: 2px solid transparent;
			
			// border: none;
			background: #fff;
			&::placeholder{
				font-size: 12px;
				opacity: 0.6;
			}
		}
		&.invalid{
			input{
				border: 2px solid tomato;
				&::placeholder{
					color: tomato;
				}
			}
		}
		margin-bottom: 10px;

	}
	&__order-button{
		display: flex;
		justify-content: center;
		align-items: center;
		background: #fff;
		border-radius: 10px;
		padding: 6px 15px;
		// overflow: hidden;
		position: relative;
		z-index: 0;
		cursor: pointer;
		// &:before{
		// 	z-index: -1;
		// 	content: '';
		// 	position: absolute;
		// 	width: 10px;
		// 	height: 10px;
		// 	top: 0;
		// 	bottom: 0;
		// 	right: 0;
		// 	left: 0;
		// 	margin: auto;
		// 	background: var(--highlightColor);
		// 	opacity: 0.4;
		// 	transform: scale(0);
		// 	transition: all 0.8s;
		// 	border-radius: 10px;
		// }
		transition: all 0.2s;
		&:hover{
				background: #aaa;
		}
	}
	&__popup-wrapper{
		position: absolute;
		top: -25px;
		left: -50px;
		right: -50px;
		display: flex;
		justify-content: center;
		transition: all 0.3s;
		opacity: 0;
		transform: scale(0);
		&.open{
			transform: scale(1);
			opacity: 1;
		}
		&.error{
			.item__popup{
				background: #ed503e;
				&:before{
					background: #ed503e;
				}
			}
		}
	}
	&__popup{
		padding: 5px;
		font-size: 14px;
		background: #3eed4f;
		border-radius: 5px;
		position: relative;

		&:before{
			z-index: -1;
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			margin-left: auto;
			margin-right: auto;
			bottom: -4px;
			width: 10px;
			height: 10px;
			background: #3eed4f;
			transform: rotate(45deg);
		}

	}
}

