
@import 'styles/utils/vars.scss'; // for width vars


.noise-overlay{
	position: absolute;
	background-image: url("../../assets/images/noise.png");
	background-repeat: repeat;
	background-size: var(--noise-size, 300px);
	width: 100%;
	height: 100%;
	z-index: -1;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	opacity: var(--noise-opacity, 0.4);
	&__wrapper{
		overflow: hidden;
		position: relative;
	}
}
.calendar-wrapper {
	position: relative;

}
.calendar-input-field {
	cursor: default;
	@media screen and (max-width: $smTabletWidth) {
		cursor: pointer;
	}

	background: linear-gradient(180deg, #7b7979 0.55%, #232020 100%);
	border-radius: 8px;
	margin-bottom: 30px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	
}
.calendar {
	position: relative;
	width: 100%;
	background: linear-gradient(180deg, #7b7979 0.55%, #232020 100%);

	border-radius: 8px;
	padding: 10px 15px;
	@media screen and (max-width: $smTabletWidth) {
		position: absolute;
		left: 0;
		right: 0;
		top: calc(100% + 30px);
		&:not(.open){
			display: none;
		}

	}
	&__month-selection {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
	}
	&__month {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 16px;
	}
	&__arrows {
		display: flex;
		align-items: center;
	}
	&__arrow {
		cursor: pointer;
		width: 11px;
		height: 16px;
		position: relative;
		padding: 3px;
		&:before, &:after{
			content:'';
			width: 10px;
			height: 3px;
			position: absolute;
			background: #fff;
			right: 3px;
			border-radius: 3px 0 0 3px;
		}
		
		&:before{
			transform-origin: 100% 100%;
			transform: rotate(45deg);
			top: calc(16px/2 - 1px); // no idea why the adjustment has to be 1px -- with height being 3px you'd think it would be 1.5

		}
		&:after{
			transform-origin: 100% 0%;
			transform: rotate(-45deg);
			bottom: calc(16px/2 - 1px); // no idea why the adjustment has to be 1px -- with height being 3px you'd think it would be 1.5
		}
		&--left {
			transform: rotate(180deg);
			margin-right: 10px;
		}
		&--right {
		}
	}
	&__day-labels {
		display: flex;
		margin-bottom: 10px;
		font-family: 'Poppins';
		font-style: normal;
		font-weight: 700;
		font-size: 12px;
	}
	&__day-label {
		flex: 0 0 calc(100% / 7);
		text-align: center;
	}
	&__days-row {
		display: flex;
		margin-bottom: 10px;
		// &:not(:last-child){
			
		// }
	}
	&__day {
		cursor: pointer;
		flex: 0 0 calc(100% / 7);
		text-align: center;
		font-family: 'Poppins';
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		transition: all 0.5s;
		&.darkened {
			opacity: 0.5;
		}
		&.disabled {
			opacity: 0.1;
			cursor: default;
		}
		&.selected {
			background: var(--highlightColor);
			border-radius: 5px;
			color: var(--textColorBG);
		}

	}
}
