
@import 'styles/utils/vars.scss'; // for width vars

.people-selector {
	width: 160px;
	@media screen and (max-width: $phoneWidth) {
		width: 200px;
	}
	&__text{
		display: flex;
		flex-direction: column;
		@media screen and (max-width: $phoneWidth) {
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;
		}
	}
	&__title {
		margin-bottom: 15px;
		font-family: 'Chivo';
		font-style: normal;
		font-weight: 700;
		font-size: 14px;
		line-height: 17px;
	}
	&__description {
		font-family: 'Chivo';
		font-style: normal;
		font-weight: 400;
		font-size: 12px;
		line-height: 20px;
		margin-bottom: 15px;
	}
	&__selector {
		display: flex;
		flex-wrap: wrap;
	}
	&__icon {
		cursor: pointer;
		width: 20%;
		svg{
			width: 100%;
		}
	}
}

