
@import 'styles/utils/vars.scss'; // for width vars
.cost{
	border-top: 1px solid white;
}
.input-field{
	cursor: default;
	background: linear-gradient(180deg, #7b7979 0.55%, #232020 100%);
	border-radius: 8px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 5px;
	&>span{
		margin-left: 8px;
	}

}
.noise-overlay{
	position: absolute;
	background-image: url("../../assets/images/noise.png");
	background-repeat: repeat;
	background-size: var(--noise-size, 300px);
	width: 100%;
	height: 100%;
	z-index: -1;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	opacity: var(--noise-opacity, 0.4);
	&__wrapper{
		overflow: hidden;
		position: relative;
	}
}
.time-selector {
	// pointer-events:none;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	&__fields {
		display: flex;
		width: 100%;
		padding: 0 5%;
		justify-content: space-around;
		margin-bottom: 32px;
		@media screen and (max-width: $phoneWidth) {
			padding: 0;
		}
	}
	&__field-description{
		font-family: 'Chivo';
		font-style: normal;
		font-weight: 900;
		font-size: 14px;
		margin-bottom: 15px;
		text-align: center;
	}
	&__field {
		flex: 0 0 33%;
		padding: 5px;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
	}
	&__content-wrapper{
		width: 100%;
		display: grid;
		grid-gap: 7px;
		grid-template-columns: repeat(auto-fit, 120px);
		@media screen and (max-width:$smDesktopWidth) {
			grid-template-columns: repeat(auto-fit, 100px);
		}
		@media screen and (max-width:1052px) {
			grid-gap: 4px;
			grid-template-columns: repeat(auto-fit, 86px);
		}
		
		justify-items: center;
		justify-content: center;
		padding: initial;
		// display: flex;
		// flex-wrap: wrap;
		// justify-content: start;
	}
	&__sector {
		transition: all 0.5s;
		// pointer-events: auto; 
		-webkit-user-select: none; /* Safari */        
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* IE10+/Edge */
		user-select: none; /* Standard */
		cursor: default;
		padding: 10px 10px;
		width: 120px;
		text-align: center;
		border: 1px solid #FFFFFF;
		border-radius: 8px;
		// margin: 7px;

		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 16px;

		@media screen and (max-width:$smDesktopWidth) {
			width: 100px;
			font-size: 12px;
		}
		@media screen and (max-width:1052px) {
			width: 86px;
			font-size: 11px;
			padding: 7px 0;
			// margin: 4px;
		}
		overflow: hidden;
		opacity: 0.2;
		&.selected{
			opacity: 0.6;
			background: var(--roomColor);
			color: var(--textColorBG);
		}
		&.border{
			opacity: 1;
			background: var(--roomColor);
			filter: drop-shadow(0px 0px 10px var(--roomColor));
			cursor: pointer;
			position: relative;
			&--start{
				// border-radius: 8px;
				// border-radius: 1px 8px 8px 1px;
			}	
			&--end{
				// border-radius: 8px 1px 1px 8px;
			}
			// background: darken(var(--roomColor), 10%);
		}
		&.expandable{
			opacity: 1;
			cursor: pointer;
		}
		&.booked{
			opacity: 1;
			background: #979797;
			cursor: default;
			.cost{
				color: #fff;
				background: #979797;
			}
			// .cost{
			// 	display: none;
			// }
			// .time{
			// 	height: 100%;
			// }
		}

	}
}
