
	@import 'styles/utils/vars.scss';
	
	.alert-modal {
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		&__title {
			color: #232020;
			font-family: 'Playfair Display';
			font-style: normal;
			font-weight: 600;
			font-size: 24px;
			line-height: 40px;
		}
		&__image {
			img{
				width: 200px;
				height: auto;
			}
		}
		&__message {
			color: #232020;
			font-family: 'Playfair Display';
			font-style: normal;
			font-weight: 600;
			font-size: 28px;
		}
		&__buttons {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-around;
			width: 100%;
		}
		&__button {
			margin: 15px 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 150px;
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 600;
			font-size: 28px;
			border-radius: 14px;
			padding: 10px;
			cursor: pointer;
			&--dismiss {
				border: 1px solid #232020;
				color: #232020;
			}
			&--accept {
				color: #fff;
				background: #902FDC;
				

			}
		}
	}
	.booking {
		&__top-bar{
			position: relative;

		}
		&__logo {
			margin: auto;
			width: 150px;
			img{
				width: 100%;
				height: auto;
			}
		}
		&__complementary-wrapper{
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__title-wrapper {
			position: absolute;
			bottom: 0;
			left: 0;
			@media screen and (max-width: $tabletWidth) {
				position: relative;
				font-size: 48px;
			}

			font-family: serif;
			font-family: 'Playfair Display';
			font-style: normal;
			font-weight: 700;
			font-size: 64px;
		}
		&__timer-wrapper {
			position: absolute;
			bottom: 0;
			right: 0;

			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 28px;
			line-height: 28px;
			@media screen and (max-width: $tabletWidth) {
				position: relative;
			}
			span{
				margin-left: 15px;
			}
			display: flex;
			align-items: center;
			color: #FFFFFF;
			&.highlighted{
				cursor: pointer;
				color: #FC4F53;
			}
		}
		&__steps-wrapper {
		}
		
		&__steps {
			margin: 30px auto 0 auto;
			width: 350px;
			@media screen and (max-width: $tabletWidth) {
				width: 280px;
			}
			// margin: auto;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__step {
			position: relative;
			cursor: default;
			&.active{
				cursor: pointer;
				.booking__step-circle{
					background: var(--roomColor);
					border: 1px solid #FFFFFF;
					color: var(--textColorBG);
					box-shadow: 4px 4px 15px rgba(255, 255, 255, 0.9);
				}
				.booking__step-text{
					color: #FFF;
					text-shadow: 4px 4px 15px rgba(255, 255, 255, 0.9);
					
				}
			}
		}
		&__step-circle{
			width: 30px;
			height: 30px;
			background: #979797;
			color: #D9D9D9;
			border-radius: 30px;
			display: flex;
			align-items: center;
			justify-content: center;

			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 16px;

			box-shadow: 4px 4px 15px rgba(255, 255, 255, 0);
			transition: all 0.5s;
		}
		&__step-text{
			text-transform: uppercase;
			position: absolute;
			bottom: 100%;
			left: 0;
			right: 0;
			display: flex;
			justify-content: center;

			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 14px;

			color: #979797;

			box-shadow: 4px 4px 15px rgba(255, 255, 255, 0);
			transition: all 0.5s;

		}
		&__steps-line{
			height: 0;
			flex-grow: 1;
			border: 1px dashed #FFFFFF;
			z-index: -5;
			transition: all 0.5s;
			&.active{
				border: 1px dashed var(--roomColor);
				box-shadow: 4px 4px 15px rgba(255, 255, 255, 0.9);

			}
		}
	}
	.loader{
		display: flex;
		min-height: 50vh;
		justify-content: center;
		align-items: center;
		.lds-roller {
		  display: inline-block;
		  position: relative;
		  width: 80px;
		  height: 80px;
		}
		.lds-roller div {
		  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
		  transform-origin: 40px 40px;
		}
		.lds-roller div:after {
		  content: " ";
		  display: block;
		  position: absolute;
		  width: 7px;
		  height: 7px;
		  border-radius: 50%;
		  background: #fff;
		  margin: -4px 0 0 -4px;
		}
		.lds-roller div:nth-child(1) {
		  animation-delay: -0.036s;
		}
		.lds-roller div:nth-child(1):after {
		  top: 63px;
		  left: 63px;
		}
		.lds-roller div:nth-child(2) {
		  animation-delay: -0.072s;
		}
		.lds-roller div:nth-child(2):after {
		  top: 68px;
		  left: 56px;
		}
		.lds-roller div:nth-child(3) {
		  animation-delay: -0.108s;
		}
		.lds-roller div:nth-child(3):after {
		  top: 71px;
		  left: 48px;
		}
		.lds-roller div:nth-child(4) {
		  animation-delay: -0.144s;
		}
		.lds-roller div:nth-child(4):after {
		  top: 72px;
		  left: 40px;
		}
		.lds-roller div:nth-child(5) {
		  animation-delay: -0.18s;
		}
		.lds-roller div:nth-child(5):after {
		  top: 71px;
		  left: 32px;
		}
		.lds-roller div:nth-child(6) {
		  animation-delay: -0.216s;
		}
		.lds-roller div:nth-child(6):after {
		  top: 68px;
		  left: 24px;
		}
		.lds-roller div:nth-child(7) {
		  animation-delay: -0.252s;
		}
		.lds-roller div:nth-child(7):after {
		  top: 63px;
		  left: 17px;
		}
		.lds-roller div:nth-child(8) {
		  animation-delay: -0.288s;
		}
		.lds-roller div:nth-child(8):after {
		  top: 56px;
		  left: 12px;
		}
		@keyframes lds-roller {
		  0% {
			transform: rotate(0deg);
		  }
		  100% {
			transform: rotate(360deg);
		  }
		}
	}
